export default {
	data() {
		return {


		}
	},
	filters: {
		numFilter(value) {
			let realMon = '';
			if (!isNaN(value) && value != '') {
				realMon = '￥' + parseFloat(value).toFixed(2);
			} else {
				realMon = '淦';
			}
			return realMon;
		}
	},
	props: [
		"shop",
		"index"
	],
	// onReachBottom(){
